import {memo, ReactNode, useEffect, useState} from 'react';
import {
    Box,
    Button,
    FormControl,
    InputLabel,
    MenuItem,
    Modal,
    Select,
    Slide,
    TextField,
    Typography
} from "@mui/material";
import useAxios from "../../providers/api/axios/axios";
import {useAppdispatch, useAppSelector} from "../../shared/hooks/Redux/redux";
import {TypeWork} from "../../providers/api/models/TypeWork";
import {typeWorkSlice} from "../../providers/api/slice/TypeWorkSlice";
import {listTypeWorksSlice} from "../../providers/api/slice/ListTypeWorksSlice";


interface CreateTypeWorkProps {
    className?: string
    children?: ReactNode
    open: boolean
    handleClose: () => void
}


export const CreateTypeWork = memo((props: CreateTypeWorkProps) => {
    const {
        className,
        children,
        open,
        handleClose,
        ...otherProps
    } = props

    const [isChange, setIsChange] = useState(false)
    const {executeRequest: req} = useAxios<TypeWork>();
    const {data, error, loading, executeRequest} = useAxios<TypeWork[]>();
    const {typeWorkApp} = typeWorkSlice.actions
    const {typeWorkReset} = typeWorkSlice.actions
    const {typeWorkName} = typeWorkSlice.actions
    const {typeWork} = useAppSelector(state => state.typeWorkSlice)
    const {listTypeWorkApp} = listTypeWorksSlice.actions
    const {listTypeWork} = useAppSelector(state => state.listTypeWorksSlice)
    useEffect(() => {
        if (data) {
            dispatch(listTypeWorkApp(data))
        }


    }, [data]);


    const dispatch = useAppdispatch()
    const changeData = async () => {
        try {

            await req('PUT', 'organization_app/update_or_create_type_of_work/', typeWork);


        } catch (error) {
            console.error('Ошибка получения данных:', error);
        }
    };
    const fetcData = async () => {
        try {

            await executeRequest('GET', 'organization_app/get_type_of_work/');


        } catch (error) {
            console.error('Ошибка получения данных:', error);
        }
    };
    const handleChange = () => {
        setIsChange(true)
        fetcData()
    }
    const handleCreate = () => {
        setIsChange(false)
        dispatch(typeWorkReset())
    }


    const style = {
        position: 'absolute' as 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: 400,
        bgcolor: 'background.paper',
        border: '2px solid #000',
        boxShadow: 24,
        p: 4,
    };


    return (
        <div>
            <Modal
                open={open}
                onClose={handleClose}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Box sx={style}>
                    <Typography id="modal-modal-title" variant="h6" component="h2">
                        Изменить или создать тип работ
                    </Typography>
                    <div>
                        <Button onClick={handleCreate}>Создать</Button>
                        <Button onClick={handleChange}>Изменить</Button>
                    </div>
                    <Slide
                        direction={"right"}
                        in={isChange} mountOnEnter unmountOnExit
                    >
                        <FormControl
                            sx={{
                                '& .MuiTextField-root': {m: 1, width: '25ch'},
                                marginBottom: "10px"
                            }}

                            fullWidth size="medium">
                            <InputLabel id="demo-select-small-label">Выбор услуги для изменения</InputLabel>
                            <Select
                                labelId="demo-select-small-label"
                                id="demo-select-small"
                                value={typeWork.name_type}
                                label="Выбор услуги"
                                // onChange={(e) => dispatch(typeWorkApp(e.target.value))}

                            >
                                {listTypeWork && listTypeWork.map((item, index) => (
                                    <MenuItem
                                        key={index}
                                        value={item.name_type}
                                        onClick={() => dispatch(typeWorkApp(item))}

                                    >
                                        {item.name_type}
                                    </MenuItem>
                                ))}
                            </Select>
                        </FormControl>

                    </Slide>
                    <TextField
                        sx={{
                            '& .MuiTextField-root': {m: 1, width: '25ch'},
                            marginBottom: "10px"
                        }}
                        error={false}
                        fullWidth
                        size="medium"
                        helperText=""
                        id="outlined"
                        label="Наименование услуги"
                        value={typeWork.name_type}
                        onChange={(e) => dispatch(typeWorkName(e.target.value))}
                    />

                    <Button onClick={() => {
                        handleClose();
                        changeData()
                    }}>Сохранить</Button>
                </Box>
            </Modal>
        </div>
    );
});