import {memo, ReactNode, useState} from 'react';
import cls from "./ListSettings.module.scss"
import Button from "@mui/material/Button";
import Accordion from '@mui/material/Accordion';
import AccordionActions from '@mui/material/AccordionActions';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import {CreateTypeWork} from "../CreateTypeWork/CreateTypeWork";
import {CreateVarietyWork} from "../CreateVarietyWork/CreateVarietyWork";

interface ListSettingsProps {
    className?: string
    children?: ReactNode
}


export const ListSettings = memo((props: ListSettingsProps) => {
    const {
        className,
        children,
        ...otherProps
    } = props
    const [openTypeWorkModal, setOpenTypeWorkModal] = useState(false)
    const [openVarietyWorkModal, setOpenVarietyWorkModal] = useState(false)
    const handleCloseTypeWorkModal =()=>{
        setOpenTypeWorkModal(false)
    }
    const handleCloseVarietyWorkModal =()=>{
        setOpenVarietyWorkModal(false)
    }


    return (
        <div className={cls.ListSettings}>
            <CreateTypeWork open={openTypeWorkModal} handleClose={handleCloseTypeWorkModal}/>
            <CreateVarietyWork open={openVarietyWorkModal} handleClose={handleCloseVarietyWorkModal}/>

            <Accordion >
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon/>}
                    aria-controls="panel1-content"
                    id="panel1-header"
                >
                    Тип услуг
                </AccordionSummary>
                <AccordionDetails>
                </AccordionDetails>
                <AccordionActions>
                    <Button>Удалить</Button>
                    <Button onClick={()=>setOpenTypeWorkModal(true)}>Изменить / Создать</Button>
                </AccordionActions>

            </Accordion>
            <Accordion>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon/>}
                    aria-controls="panel2-content"
                    id="panel2-header"
                >
                    Разновидность услуг
                </AccordionSummary>
                <AccordionDetails>
                </AccordionDetails>
                <AccordionActions>
                    <Button>Удалить</Button>
                    <Button onClick={()=>{setOpenVarietyWorkModal(true)}}>Изменить</Button>
                </AccordionActions>

            </Accordion>
            <Accordion defaultExpanded>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon/>}
                    aria-controls="panel3-content"
                    id="panel3-header"
                >
                    Акции
                </AccordionSummary>
                <AccordionDetails>
                </AccordionDetails>
                <AccordionActions>
                    <Button>Удалить</Button>
                    <Button>Изменить</Button>
                </AccordionActions>
            </Accordion>

        </div>
    );
});