import {memo, ReactNode, useEffect, useState} from 'react';
import {
    Box,
    Button,
    FormControl,
    InputLabel,
    MenuItem,
    Modal,
    Select,
    Slide,
    TextField,
    Typography
} from "@mui/material";
import useAxios from "../../providers/api/axios/axios";
import {TypeWork} from "../../providers/api/models/TypeWork";
import {typeWorkSlice} from "../../providers/api/slice/TypeWorkSlice";
import {useAppdispatch, useAppSelector} from "../../shared/hooks/Redux/redux";
import varietyWorksSlice, {varietyWorkSlice} from "../../providers/api/slice/VarietyWorkSlice";
import {listVarietyWorksSlice} from "../../providers/api/slice/ListVarietyWorkSlice";
import {VarietyWorkType} from "../../providers/api/models/VarietyWorkType";
import {listTypeWorksSlice} from "../../providers/api/slice/ListTypeWorksSlice";

interface CreateVarietyWorkProps {
    className?: string
    children?: ReactNode
    open: boolean
    handleClose: () => void

}


export const CreateVarietyWork = memo((props: CreateVarietyWorkProps) => {
    const {
        className,
        children,
        open,
        handleClose,
        ...otherProps
    } = props

    const [isChange, setIsChange] = useState(false)
    const {executeRequest: req} = useAxios<VarietyWorkType>();
    const {data: typeDate, executeRequest: reqType} = useAxios<TypeWork[]>();
    const {data, error, loading, executeRequest} = useAxios<VarietyWorkType[]>();
    const dispatch = useAppdispatch()
    const {varietyWorkApp} = varietyWorkSlice.actions
    const {varietyWorkName} = varietyWorkSlice.actions
    const {varietyWorkCost} = varietyWorkSlice.actions
    const {varietyWorkMinutes} = varietyWorkSlice.actions
    const {varietyWorkType} = varietyWorkSlice.actions
    const {varietyWorkReset} = varietyWorkSlice.actions
    const {varietyWork} = useAppSelector(state => state.varietyWorksSlice)
    const {listVarietyWorkApp} = listVarietyWorksSlice.actions
    const {listVarietyWork} = useAppSelector(state => state.listVarietyWorksSlice)
    const {typeWork} = useAppSelector(state => state.typeWorkSlice)
    const {listTypeWorkApp} = listTypeWorksSlice.actions
    const {listTypeWork} = useAppSelector(state => state.listTypeWorksSlice)


    const changeData = async () => {
        try {

            await req('PUT', 'organization_app/update_or_create_variety_of_work/', varietyWork);


        } catch (error) {
            console.error('Ошибка получения данных:', error);
        }
    };
    const fetcData = async () => {
        try {

            await executeRequest('GET', 'organization_app/get_variety_of_work/');


        } catch (error) {
            console.error('Ошибка получения данных:', error);
        }
    };

    const getType = async () => {
        try {

            await reqType('GET', 'organization_app/get_type_of_work/');


        } catch (error) {
            console.error('Ошибка получения данных:', error);
        }
    };
    useEffect(() => {
        getType()
    }, []);

    useEffect(() => {
        if (typeDate) {
            dispatch(listTypeWorkApp(typeDate))

        }
    }, [typeDate]);

    useEffect(() => {
        console.log(data)
        if (data) {
            dispatch(listVarietyWorkApp(data))


        }
    }, [data]);
    useEffect(() => {
        console.log(varietyWork,data)
    }, [varietyWork]);


    const handleChange = () => {
        setIsChange(true)
        fetcData()
    }
    const handleCreate = () => {
        setIsChange(false)
        dispatch(varietyWorkReset())
    }


    const style = {
        position: 'absolute' as 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: 400,
        bgcolor: 'background.paper',
        border: '2px solid #000',
        boxShadow: 24,
        p: 4,
    };


    return (
        <div>
            <Modal
                open={open}
                onClose={handleClose}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Box sx={style}>
                    <Typography id="modal-modal-title" variant="h6" component="h2">
                        Изменить или создать тип работ
                    </Typography>
                    <div>
                        <Button onClick={handleCreate}>Создать</Button>
                        <Button onClick={handleChange}>Изменить</Button>
                    </div>
                    <Slide
                        direction={"right"}
                        in={isChange} mountOnEnter unmountOnExit
                    >
                        <FormControl
                            sx={{
                                '& .MuiTextField-root': {m: 1, width: '25ch'},
                                marginBottom: "10px"
                            }}

                            fullWidth size="medium">
                            <InputLabel id="demo-select-small-label">Выбор типа работ для изменения</InputLabel>
                            <Select
                                labelId="demo-select-small-label"
                                id="demo-select-small"
                                value={varietyWork.name_variety}
                                label="Выбор услуги"
                                onChange={(e) => dispatch(varietyWorkName(e.target.value))}

                            >
                                {listVarietyWork && listVarietyWork.map((item, index) => (
                                    <MenuItem
                                        key={index}
                                        value={item.name_variety}
                                        onClick={() =>{
                                            dispatch(varietyWorkApp(item));
                                            // console.log(item)
                                            // dispatch(varietyWorkName(item.name_variety));
                                            // dispatch(varietyWorkCost(Number(item.cost)));
                                            // dispatch(varietyWorkMinutes(Number(item.duration_in_minutes)));
                                            // dispatch(varietyWorkType(item.type_work));
                                    }}

                                    >
                                        {item.name_variety}
                                    </MenuItem>
                                ))}
                            </Select>
                        </FormControl>

                    </Slide>
                    <TextField
                        sx={{
                            '& .MuiTextField-root': {m: 1, width: '25ch'},
                            marginBottom: "10px"
                        }}
                        error={false}
                        fullWidth
                        size="medium"
                        helperText=""
                        id="outlined"
                        label="Разновидность услуги"
                        value={varietyWork.name_variety}
                        onChange={(e) => dispatch(varietyWorkName(e.target.value))}
                    />
                    <TextField
                        sx={{
                            '& .MuiTextField-root': {m: 1, width: '25ch'},
                            marginBottom: "10px"
                        }}
                        error={false}
                        fullWidth
                        size="medium"
                        helperText=""
                        id="outlined"
                        label="Стоимость работы"
                        type={"number"}
                        value={varietyWork.cost}
                        onChange={(e) => dispatch(varietyWorkCost(Number(e.target.value)))}
                    />
                    <TextField
                        sx={{
                            '& .MuiTextField-root': {m: 1, width: '25ch'},
                            marginBottom: "10px"
                        }}
                        error={false}
                        fullWidth
                        size="medium"
                        helperText=""
                        id="outlined"
                        label="Длительность работы в минутах"
                        type={"number"}
                        value={varietyWork.duration_in_minutes}
                        onChange={(e) => dispatch(varietyWorkMinutes(Number(e.target.value)))}
                    />
                    <FormControl
                        sx={{
                            '& .MuiTextField-root': {m: 1, width: '25ch'},
                            marginBottom: "10px"
                        }}

                        fullWidth size="medium">
                        <InputLabel id="demo-select-small-label">Тип услуги</InputLabel>
                        <Select
                            labelId="demo-select-small-label"
                            id="demo-select-small"
                            value={varietyWork.type_work}
                            label="Тип услуги"
                            onChange={(e) => dispatch(varietyWorkType(e.target.value))}

                        >
                            {listTypeWork && listTypeWork.map((item, index) => (
                                <MenuItem
                                    key={index}
                                    value={item.name_type}
                                    onClick={() => dispatch(varietyWorkType(item.name_type))}

                                >
                                    {item.name_type}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>


                    <Button onClick={() => {
                        handleClose();
                        changeData()
                    }}>Сохранить</Button>
                </Box>
            </Modal>

        </div>
    );
});